import Link from "next/link";
import PokeLogo from "../../ScorpionWebPoke.png"
export default function Navbar(){
    return(
        <nav>
            <div>
                <Link href="/"><Image src={PokeLogo} width={100} height={100} alt="PokeLogo"/></Link>
                <h1>PokeScorpion</h1>
            </div>
        </nav>
    );
}